$(function () {
    var counter = $('.counter'),
        input = counter.find('input');
    
    counter.on('click', '.counter__plus', function () {
        var input = $(this).siblings('input');
        input.val(Number(input.val()) + 1);
    });
    
    counter.on('click', '.counter__minus', function () {
        var input = $(this).siblings('input');
        
        if (Number(input.val()) > 1) {
            input.val(Number(input.val()) - 1);
        }
    });
    
    input.on('keydown', function (e) {
        if ((e.keyCode < 48 || e.keyCode > 57) && (e.keyCode !== 8 && e.keyCode !== 45 && e.keyCode !== 46)) {
            e.preventDefault();
        }
    });
    
    input.on('focusout', function () {
       if ($(this).val() === '') {
           $(this).val(1);
       }
    });
});