$(function () {
    if (window.matchMedia("(min-width: 768px)").matches) {
        let page = $(window),
            navigation = $('.navigation'),
            navigationHidden = $('.navigation_hidden'),
            header = $('.header');
    
        toggleNav(page);
    
        page.on('scroll', function (e) {
            toggleNav($(this));
        });
    
        function toggleNav (page) {
            let distance = header.height();
        
            if (page.scrollTop() > distance - navigation.height()) {
                if (!$('.main-menu').hasClass('open')) {
                    navigation.hide();
                }
            
                navigationHidden.show();
            
                $('body').css({
                    'padding-top': navigation.height() + 'px'
                });
            } else {
                $('body').css({
                    'padding-top': '0'
                });
                navigation.show();
                navigationHidden.hide();
            }
        }
    }
});