import Swiper from 'swiper/dist/js/swiper.min.js';

$(function () {
    var mySwiper = new Swiper('.slider-main', {
        slidesPerView: 2,
        spaceBetween: 37,
        navigation: {
            nextEl: '.slider-main-wrapper__next',
            prevEl: '.slider-main-wrapper__prev',
        },
        autoplay: {
            delay: 5000,
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            768: {
                slidesPerView: 1
            },
            1025: {
                slidesPerView: 1
            }
        }
    });
});