$(function () {
   let button = $('.search-hidden a'),
       field = $('.navigation__form-field');
   
   button.on('click', function (e) {
       e.preventDefault();
    
       field.css({
           width: '100%',
           'z-index': 5
       });
   });
   
   if (button.css('display') === 'block') {
       field.on('focusout', function (e) {
           e.preventDefault();
           
           $(this).css({
               width: '0',
               'z-index': 0
           });
       });
   }
});