$(function () {
    $('.dropdown-toggle').on('mouseover', function (e) {
        e.preventDefault();
        
        $('#' + $(this).attr('drop-menu')).addClass('open');
    });
    
    $('.dropdown-toggle').on('mouseout', function (e) {
        e.preventDefault();
        
        $('#' + $(this).attr('drop-menu')).removeClass('open');
    });
});